import { HttpClient, HttpParams, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from 'src/environments/environment';
const httpOptions = {
    responseType: 'blob' as 'json',
};
@Injectable({ providedIn: "root" })
export class CommonService {
    baseUrl = `${environment.serviceBaseUrl}`;

    constructor(private httpClient: HttpClient) { }

    getToken() {
        return this.httpClient.get(
            this.baseUrl + "api/authenticate/v1/token"
        )
    }

    getDashboardCount(CustomerId) {
        return this.httpClient.get(
            this.baseUrl + "api/proposal/dashboardCount/" + CustomerId
        )
    }



    getCustomerType() {
        return this.httpClient.get(
            this.baseUrl + "api/product/v1/customerTypeData",
        );
    }

    getLOBs() {
        return this.httpClient.get(
            this.baseUrl + "api/product/lineOfBusiness"
        );
    }

    getProductsByLOB() {
        return this.httpClient.get(
            this.baseUrl + "api/product/LOBList"
        );
    }

    getSalutation(customerType) {
        switch (customerType) {
            case "Individual": return this.httpClient.get(
                this.baseUrl + "api/product/v1/getSalutations"
            )
        }
    }

    getProductsByLOBCode(lobCode: string) {
        return this.httpClient.get(
            this.baseUrl + "api/product/products/" + lobCode
        );
    }

    buildingHeightData() {
        return this.httpClient.get(
            this.baseUrl + "api/proposal/v1/buildingHeightData",
        );
    }

    agreementTypeData() {
        return this.httpClient.get(
            this.baseUrl + "api/proposal/v1/agreementTypeData",
        );
    }

    getCoverages() {
        return this.httpClient.get(
            this.baseUrl + "api/proposal/v1/coverages",
        );
    }

    downloadCustomerProposalDataBrochure(data) {
        return this.httpClient.post(this.baseUrl + "api/document/staticDocumentDownload", data
            , httpOptions);
    }
    getCustomerProposal() {
        return this.httpClient.get(
            this.baseUrl + "api/proposal/202008190000115"              );
    }
}
