import { HttpClient, HttpParams, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
let headers1: HttpHeaders = new HttpHeaders();
headers1 = headers1.append('Content-Type', 'application/json');
headers1 = headers1.append('Authorization', 'application/json');

@Injectable({ providedIn: "root" })
export class CreateQuoteService {
  constructor(private httpClient: HttpClient) { }
  baseUrl = `${environment.serviceBaseUrl}`;

  searchCustomerDetails(searchCustomerDetails) {
    return this.httpClient.post(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7777/searchCustomer",
      searchCustomerDetails, {headers: headers1}
    );
  }

  makePayment(inputForPayment) {
    return this.httpClient.post(
      this.baseUrl + "api/payment/gatewayPayU", inputForPayment, { headers: headers1, responseType: 'text' }
    );
  }

  saveNewProposal(propsalDetails) {
    return this.httpClient.post(
      this.baseUrl + "api/proposal/saveProposalInDb", propsalDetails, { headers: headers1, responseType: 'text' }
    );
  }




  saveUserProfileDetails(searchCustomerDetails) {
    return this.httpClient.post(
      this.baseUrl + "api/agent/v1/saveUserProfileDetails",
      searchCustomerDetails
    );
  }

  userProfileDetails(searchParams) {
    return this.httpClient.get(
      this.baseUrl + "api/agent/v1/userProfileDetails/" + searchParams.imdCode,
      {
        headers: {
          'token': searchParams.token
        }
      }
    );
  }

  getCustomerDetailsById(searchCustomerDetails) {
    return this.httpClient.post(
      this.baseUrl + "api/customer/v1/searchLovCustomer",
      searchCustomerDetails
    );

  }

  createQuoteDetails(createQuoteDetails) {
    return this.httpClient.post(
      this.baseUrl + "api/customer/v1/duplicateCustomer",
      createQuoteDetails
    );
  }

  getVirtualWallet(userID) {
    return this.httpClient.get(
       "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7776/balance/" + userID
    );
  }

  getNewProposal(quoteNumber) {
    return this.httpClient.get(
      this.baseUrl + "api/quote/" + quoteNumber,
    );
  }

  uploadFile(data: FormData): Observable<string> 
  {
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'multipart/form-data');
    const httpOptions = { headers: headers };
    return this.httpClient.post<string>('http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7779/excelupload',
    data,httpOptions);
  }

  retryuploadFile(data: FormData): Observable<string> 
  {
    let headers = new HttpHeaders();
    headers.append('Content-Type', 'multipart/form-data');
    const httpOptions = { headers: headers };
    return this.httpClient.post<string>('http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7779/excelreupload',
    data,httpOptions);
  }

  getuploadedData()
  {
    return this.httpClient.get(
      "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7779/tutorials"
    );
  }
}