import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
})
export class HomeComponent implements OnInit {
  token: any;
  mini = true;
  walletResponse: any;
  userID: number;
  constructor(private commonService: CommonService, public datepipe: DatePipe, private createQuoteService: CreateQuoteService
  ) {
    this.gettoken();
  }

  ngOnInit() {
    this.getVirtualWallet();
  }

  toggleSidebar() {
    if (this.mini) {
      document.getElementById("mySidebar").style.width = "240px";
      document.getElementById("main").style.marginLeft = "236px";
      var elem = document.getElementById("mySidebar");
      elem.classList.remove("highlight");
      this.mini = false;
    } else {
      document.getElementById("mySidebar").style.width = "3.9%";
      document.getElementById("main").style.marginLeft = "52px";
      var elem = document.getElementById("mySidebar");
      elem.className += " highlight";
      this.mini = true;
    }
  }

  gettoken() {
    this.commonService.getToken().subscribe(
      (res) => {
        this.token = res;
      },
      (error: HttpErrorResponse) => {
        localStorage.setItem("token", error.error.text);
      }
    );
  }

  getVirtualWallet() {
    this.userID = 1;
    this.createQuoteService.getVirtualWallet(this.userID).subscribe(
      (res) => {
        this.walletResponse = res;
        this.walletResponse = this.walletResponse.closingBalance;
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }
}
