import { Component, OnInit,Output,EventEmitter,ChangeDetectorRef } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { Okta } from 'src/app/core/services/okta.service';
//import { Component, ChangeDetectorRef, OnInit } from '@angular/core';

@Component({
  selector: 'app-login-page',
  templateUrl: "./login-page.component.html",
  styleUrls: ["./login-page.component.css"],
})
export class LoginPageComponent implements OnInit {
  user;
  oktaSignIn;

  @Output() toggle = new EventEmitter<any>();
  loginFlage: boolean = true;

  constructor(
  public datepipe: DatePipe,
  private createQuoteService: CreateQuoteService, 
  private commonService: CommonService,
  private okta: Okta, 
  private changeDetectorRef: ChangeDetectorRef)
  {
    this.oktaSignIn = okta.getWidget();
  }

  //login()
  //{
    //this.sideNavOpen = !this.sideNavOpen;
    //this.toggle.emit(this.loginFlage);
  //}

  async ngOnInit(): Promise<void> {
    try {
      this.user = await this.oktaSignIn.authClient.token.getUserInfo();
    } catch (error) {
      this.showLogin();
    }
  }

  showLogin(): void {
    this.oktaSignIn.renderEl({el: '#okta-login-container'}, (response) => {
      if (response.status === 'SUCCESS') {
        this.user = response.tokens.idToken.claims.email;
        this.toggle.emit(this.loginFlage);
        this.oktaSignIn.remove();
        this.changeDetectorRef.detectChanges();
      }
    });
  }

  logout(): void {
    this.oktaSignIn.authClient.signOut(() => {
      this.user = undefined;
      this.showLogin();
    });
  }
}
