import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomeComponent } from './modules/home/home.component';

const routes: Routes = [
  {
    path: '', component: HomeComponent,
    
    children: [
      {
        path: '',
        loadChildren: () => import('src/app/modules/section-dashboard/section-dashboard.module').then(m => m.SectionDashboardModule)
      },
      {
        path: 'create-quote',
        loadChildren: () => import('src/app/modules/create-quote/create-quote.module').then(m => m.CreateQuoteModule)
      },
      {
        path: 'file-upload',
        loadChildren: () => import('src/app/modules/file-upload/file-upload.module').then(m => m.FileUploadModule)
      },
      {
        path: 'ocr-upload',
        loadChildren: () => import('src/app/modules/ocr-upload/ocr-upload.module').then(m => m.OcrUploadModule)
      },
      {
        path: 'file-reupload',
        loadChildren: () => import('src/app/modules/file-reupload/file-reupload.module').then(m => m.FileReUploadModule)
      },
      {
        path: 'topic-dashboard/:item.section.id/:item.section.name',
        loadChildren: () => import('src/app/modules/topic-dashboard/topic-dashboard.module').then(m => m.TopicDashboardModule)
      },
      {
        path: 'post-dashboard/:item.id/:item.content',
        loadChildren: () => import('src/app/modules/post-dashboard/post-dashboard.module').then(m => m.PostDashboardModule)
      },
      {
        path: 'create-section',
        loadChildren: () => import('src/app/modules/create-section/create-section.module').then(m => m.CreateSectionModule)
      },
      {
        path: 'create-topic',
        loadChildren: () => import('src/app/modules/create-topic/create-topic.module').then(m => m.CreateTopicModule)
      },
      {
        path: 'create-post',
        loadChildren: () => import('src/app/modules/create-post/create-post.module').then(m => m.CreatePostModule)
      },
      {
        path: 'wallet',
        loadChildren: () => import('src/app/modules/walletstatement/walletstatement.module').then(m => m.WalletstatementModule)
      },
      
      {
        path: 'search',
        loadChildren: () => import('src/app/modules/search/search.module').then(m => m.SearchModule)
      },
      {
        path: 'payment-proposal',
        loadChildren: () => import('src/app/modules/payment-proposals/payment-proposals.module').then(m => m.PaymentProposalModule)
      },
      {
        path: 'my-profile',
        loadChildren: () => import('src/app/modules/profile/profile.module').then(m => m.ProfileModule)
      },
      {
        path: 'payments',
        loadChildren: () => import('src/app/modules/payments/payments.module').then(m => m.PaymentsModule)
      }
    ],
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule { }
